.package {
  box-sizing: border-box;
  padding: 20px;

  background-color: #f4f4f4;
  width: 100%;

  &--main {
    background-color: #5a3661;
  }

  &__icon {
    width: 88px;
    height: 82px;
  }

  &__price {
    font-weight: 700;
    font-size: 40px;
    line-height: 52px;
    color: #a575ae;

    &--amount {
      margin: 16px 0;
    }

    &--name {
      font-size: 24px;
      line-height: 31px;

      margin-bottom: 8px;
    }

    &--cashback {
      margin: 0;
    }
  }

  &__text {
    font-size: 16px;
    line-height: 24px;
    color: #3f3e46;
    font-weight: 400;
    
    &--content {
      margin: 32px 0;
      white-space: pre-line;
    }

    &--white {
      color: #fff;
    }
  }

  &__cashback {
    font-size: 16px;
    line-height: 20px;
    color: #000;
    font-weight: 700;

    &--white {
      color: #fff;
    }
  }

  &__button {
    width: 100%;
    height: 40px;
    background-color: #a575ae;

    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 32px;

    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;

    &:hover {
      box-sizing: border-box;
      border: 2px solid #a575ae;
      background-color: #fff;
      color: #a575ae;
    }
  }
}
